@media (max-width: 729.98px) {
  .main-header__row {
    justify-content: center;
  }

  .main-header__burger {
    display: block;
    width: 30px;
    height: 20px;
    z-index: 4;
    background-color: transparent;
    position: absolute;
    top: 24px;
    left: 15px;
    border: none;
    padding: 0;
    cursor: pointer;
  }

  .main-header__burger:hover,
  .main-header__burger:focus {
    outline: none;
  }

  .main-header__burger span {
    background-color: #fd9334;
    position: absolute;
    left: 0;
    width: 100%;
    height: 2px;
    top: 9px;
    transition: all 0.3s ease 0s;
  }

  .main-header__burger::before,
  .main-header__burger::after {
    content: "";
    background-color: #fd9334;
    position: absolute;
    width: 100%;
    height: 2px;
    left: 0;
    transition: all 0.3s ease 0s;
  }

  .main-header__burger::before {
    top: 0;
  }

  .main-header__burger::after {
    bottom: 0;
  }

  .main-header__burger.active span {
    transform: scale(0);
  }

  .main-header__burger.active::before {
    transform: rotate(45deg);
    top: 9px;
    background-color: #ffffff;
  }

  .main-header__burger.active::after {
    transform: rotate(-45deg);
    bottom: 9px;
    background-color: #ffffff;
  }

  .main-header.active::before {
    transition: all 0.3s ease 0s;
    background-color: #44413b;
    z-index: 4;
  }

  .navigation {
